import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { FieldsetProps } from './Fieldset.interface';

export const Fieldset: React.FC<FieldsetProps> = ({
  messageId,
  messageAriaLive,
  children,
  state = 'default',
  message,
  disabled = false,
  ...props
}) => {
  return (
    <fieldset
      className={clsx('zep-inline-flex', 'zep-flex-col', 'zep-flex-nowrap', [
        disabled ? 'zep-cursor-not-allowed zep-opacity-disabled' : 'zep-cursor-pointer',
      ])}
      data-testid="zep-form-fieldset"
      {...props}
    >
      {children}
      {message && (
        <span
          id={messageId}
          aria-live={messageAriaLive}
          className={clsx(
            {
              'zep-text-error': state === 'error',
              'zep-text-info': state === 'info',
              'zep-text-typography-dark-100': state === 'default',
            },
            'zep-mt-0.25 zep-flex zep-flex-row zep-gap-0.5 zep-text-0.875',
          )}
        >
          {state === 'error' && (
            <FunctionalIcon name="warning-triangle-filled" className="zep-flex-shrink-0 zep-text-error" />
          )}
          {state === 'info' && <FunctionalIcon name="info-circle-filled" className="zep-flex-shrink-0 zep-text-info" />}
          <span className="zep-mt-0.125">{message}</span>
        </span>
      )}
    </fieldset>
  );
};
